import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Container, Grid, Typography } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'

import ArtworkCard from '../../components/Card/ArtworkCard'
import Cover from './Cover'

import { selectLiveArtworks } from '../../store/artwork/artworkSlice'

import './home.css'

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
}))

const Home = () => {
  const classes = useStyles()

  // const artworks = useSelector((state) => state.artwork.artworks) selectLiveArtworks
  const artworks = useSelector((state) => selectLiveArtworks(state))
  let artwork = artworks[Math.floor(Math.random() * artworks.length)]

  if (!artwork) {
    // remove this later temp image 
    artwork = {
      owner: '0x61f389d2b2fdd8c56120e1200202c38cbf1aee40',
      username: 'randyortan',
      artwork: 'QmSQagpSg1tHJkyXEa1HrB9imapcxPDBSXhaUdRDehq6o5',
      description: 'the camel is a unqie colored one',
      metadata: 'QmeEn1vfHfJFwGExnGxVZVqbLjqySxnKtwgUfgVHK6ptLD',
      title: 'Rainbow Camel',
      price: 0,
      endTime: Date.now()
    }
  }

  return (
    <Container>
      <Cover artwork={artwork} />
      <Typography
        style={{
          paddingTop: '96px',
          paddingBottom: '24px',
          borderBottom: '2px solid',
          marginBottom: '24px',
        }}
        variant="h5"
        component="h6"
      >
        <span>-</span> Live auctions{' '}
      </Typography>

      <div>
        <Grid
          direction="row"
          justify="flex-start"
          alignItems="center"
          justify="center"
          container
          className={classes.root}
          spacing={2}
        >
          <Grid item xs={12}>
            <Grid container spacing={3}>
              {artworks &&
                artworks.map((artwork) => {
                  return (
                    <Grid key={artwork._id} item md={3}>
                      <ArtworkCard artwork={artwork} />
                    </Grid>
                  )
                })}
            </Grid>
          </Grid>
        </Grid>
      </div>
    </Container>
  )
}
export default Home
