import React, { useEffect, useState } from 'react'
import { NavLink as RouterLink, Link } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
//import { web3, connectMetaMask } from '../../../services/web3'
import { Toolbar, Button, Container, Backdrop } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import { getCurrentBalance } from '../../../services/Web3/web3'
import MetaMask from '../../MetaMask'
import { logOut } from '../../../store/user/userSlice'

import Logo from '../../../images/logo/icon/logo_icon_color.png'
import Dropdown from '../../Drowndown/index'

//import {Navbar, NavItem, DropdownMenu} from '../../Navbar/Navbar'
import Navbar from '../../Navbar/Navbar'
//import NavItem from '../../Navbar/NavItem'

import { ReactComponent as CaretIcon } from '../../../images//icons/caret.svg'

const useStyles = makeStyles(() => ({
  logo: {
    fontFamily: 'Work Sans, sans-serif',
    fontWeight: 600,
    textAlign: 'left',
  },
  toolbar: {
    display: 'flex',
    justifyContent: 'space-between',
  },
}))

const Desktop = (props) => {
  const dispatch = useDispatch()
  const userSlice = useSelector((state) => state.user)
  const { toolbar } = useStyles()

  const [showMetaMaskModal, setShowMetaMaskModal] = useState(false)
  const [dropdownMenu, setDropdownMenu] = useState(false)
  const [balance, setBalance] = useState(0)

  const onClickConnect = () => {
    setShowMetaMaskModal(true)
  }

  const closeMetaMaskModal = () => {
    setShowMetaMaskModal(false)
  }

  const handleLogOut = () => {
    dispatch(logOut())
  }

  // const handleOpenMenu = (event) => {
  //   setDropdownMenu(!dropdownMenu)
  // }

  // const handleMenuClose = () => {
  //   setDropdownMenu(false)
  // }

  useEffect(() => {
    if (userSlice.user) {
      ;(async () => {
        let balance = await getCurrentBalance()
        balance = parseFloat(balance)

        balance = balance.toFixed(4)
        setBalance(balance)
      })()
    }
  }, [userSlice.user])

  // function NavItem(props) {
  //   const [open, setOpen] = useState(false)

  //   return (
  //     <li className="nav-item">
  //       <a href="#" className="icon-button" onClick={() => setOpen(!open)}>
  //         {props.icon}
  //       </a>

  //       {open && props.children}
  //     </li>
  //   )
  // }

  const UserButton = ({ user }) => {
    const [open, setOpen] = useState(false)

    const { publicAddress, username } = user
    const subKey = `${publicAddress.slice(0, 4)}...${publicAddress.slice(
      publicAddress.length - 4,
      publicAddress.length,
    )}`

    return (
      <div>
        <Link to="/bids" className="bid-btn">
          bids
        </Link>

        <Link to="/creator/create" className="create-btn">
          Create
        </Link>

        <div className="user-container" >
        <Button onClick={() => setOpen(!open)} className="user-btn">
            <div className="user-btn-content">
              <h6>{balance} ETH</h6>
              <p>{subKey}</p>
            </div>
            <div className="userAvatar">
              <div className="userAvatarContainer"></div>
              <div className="online-mark"></div>
            </div>
          </Button>
          {open &&(<Dropdown id={username? username: publicAddress} handleLogOut={handleLogOut} />) }
          {open &&( <Backdrop open={open}   onClick={() => setOpen(!open)}></Backdrop>) }
          
        </div>
       
      </div>
    )
  }

  return (
    <Container maxWidth="lg">
      <Navbar className={toolbar}>
        <div>
        <RouterLink exact to="/">
        <img className="logo" src={Logo} />
              </RouterLink>

          
        </div>

        <nav>
          <ul
            style={{
              display: 'flex',
              alignItems: 'center',
              borderRadius: '15px',
              padding: '1% 1%',
              background:' #fff'
            }}
          >
            <li>
              <RouterLink exact activeClassName="active" to="/artworks">
                Artwork
              </RouterLink>
            </li>

            <li>
              <RouterLink exact activeClassName="active" to="/">
                Home
              </RouterLink>
            </li>
            <li>
              <RouterLink exact activeClassName="active" to="/creators">
                Creators
              </RouterLink>
            </li>
          </ul>
        </nav>

        {userSlice.user ? (
          <UserButton user={userSlice.user}></UserButton>
        ) : (
          <Button
            onClick={onClickConnect}
            className="connect-button"
            size="medium"
          >
            Connect Wallet{' '}
          </Button>
        )}
      </Navbar>

      <MetaMask
        showModal={showMetaMaskModal}
        handleClose={closeMetaMaskModal}
      />
    </Container>
  )
}

export default Desktop
