import React from 'react'
import { Grid, Button, makeStyles } from '@material-ui/core'
import { Link } from 'react-router-dom'
import { web3Utiles } from '../../services/web3'
import Countdown from '../../components/Countdown'
import Clip from '../../components/Clip'
import {isVideo} from '../../utils'

const IMG_BASE = 'https://ipfs.io/ipfs/'
const ETH_RATE = 2082.87


const useStyles = makeStyles(() => ({
  bgBtn: {
    padding: '16px 34px',
    transition: 'all 300ms cubic-bezier(0.23,1,0.32,1)',
    border: 'solid 2px',
    borderColor: '#4e61ff',
    minHeight: '60px',
    borderRadius: '18px',
    '&:hover': {
      boxShadow: '0px 8px 15px rgb(0 0 0 / 25%)',
      transform: 'translateY(-2px)',
    },
  },
  blackBtn: {
    backgroundColor: '#4e61ff',
    color: '#fff',
    '&:hover': {
      backgroundColor: '#4e61ff',
      color: '#FFFFFF',
    },
  },
  oBlackBtn: {
    backgroundColor: '#fff',
    color: '#000',
    '&:hover': {
      backgroundColor: '#fff',
      color: '#000',
    },
  },
  authorContainer: {
    backgroundColor: '#fff',
    borderRadius: '18px',
    border: 'solid 2px #4e61ff',
    padding: '8px 24px',
    textDecoration: 'none',
  },
  sub_text: {
    fontSize: '18px',
    fontWeight: '600',
    color: '#666',
  },
  bold_text: {
    display: 'block',
    width: '100%',
    fontWeight: '600',
    marginBottom: '10px',
    fontSize: '36px',
  },
}))

const Cover = ({ artwork }) => {
  const classes = useStyles()

  const renderPrice = () => {
    let price =  artwork.price  / 1e18 //window.web3.utils.fromWei((artwork.price).toString(), 'ether')

    return (
      <div>
        <div
          style={{
            width: '100%',
            fontWeight: '600',
            fontSize: '16px',
            marginBottom: '5px',
          }}
        >
          Current Bid
        </div>
        <div className={classes.bold_text}>
          {price} {'ETH'}
        </div>
        <div className={classes.sub_text}>${(ETH_RATE * parseFloat(price)).toFixed(2) }</div>
      </div>
    )
  }


  return (
    <div>
      <Grid container>
        <Grid container item xs={12} md={6}>
          <div style={{ padding: 80 }}>

                {isVideo(artwork.artwork)? (
                  <Clip url={IMG_BASE + artwork.artwork}   className="video" />
              ):(
              <img style={{ width: '100%' }} src={IMG_BASE + artwork.artwork} />
              )}

          </div>
        </Grid>
        <Grid container item xs={12} md={6}>
          <div style={{ flexDirection: 'row', padding: 100 }}>
            <div>
              <Link
                className={classes.authorContainer}
                to={'/' + artwork.username}
              >
                {artwork.username}
              </Link>
            </div>

            <h1>{artwork.title}</h1>
            <Grid container>
              <Grid container item xs={12} md={6}>
                {renderPrice()}
              </Grid>

              <Grid container item xs={12} md={6}>
                <div
                  style={{
                    width: '100%',
                    fontWeight: '600',
                    fontSize: '16px',
                    marginBottom: '5px',
                  }}
                >
                  Auction ending in
                </div>
                <Countdown type="main-countdown" endTime={artwork.endTime} />
              </Grid>
            </Grid>

            <Grid container style={{ padding: '5% 0' }}>
              <Grid container item xs={12} md={6}>
                <Link  to={`/${artwork.username}/${artwork.title}`} style={{ textDecoration: 'none' }}>
                  <Button className={`${classes.bgBtn}  ${classes.blackBtn}`}>
                    {' '}
                    Place a bid{' '}
                  </Button>
                </Link>
              </Grid>
              <Grid container item xs={12} md={6}>
                <Link
                  to={`/${artwork.username}/${artwork.title}`}
                  style={{ textDecoration: 'none' }}
                >
                  <Button className={`${classes.bgBtn}  ${classes.oBlackBtn}`}>
                    {' '}
                    View Artwork{' '}
                  </Button>
                </Link>
              </Grid>
            </Grid>
          </div>
        </Grid>
      </Grid>
    </div>
  )
}
export default Cover
