import React, { useState,useRef, useEffect } from 'react'
import { Link } from "react-router-dom";
import { makeStyles } from '@material-ui/core/styles';
import Backdrop from '@material-ui/core/Backdrop';
import { CSSTransition } from 'react-transition-group';

//import './index.css'

const useStyles = makeStyles((theme) => ({
    root: {
        position: 'absolute',
        right: '20px',
        top: '10%',
        opacity: 1,
        transform: 'none'
    },
    dropdownContainer: {
        boxShadow: ' rgba(0, 0, 0, 0.1) 0px 10px 20px',
        position: 'absolute',
        right: '180px',
        top: 'calc(100% + 16px)',
        zIndex: '9999',
        backgroundColor: ' #FFFFFF',
        borderRadius: '10px',
        minWidth: '300px',
    },
    userItem: { //css-z1zlzs
        padding: '16px',
        boxShadow: 'rgba(0, 0, 0, 0.05) 0px 10px 20px',
        borderRadius: '10px',
    },
    userLink: {
        padding: ' 14px 16px 12px',
        display: 'block',
        borderRadius: '8px',
        boxShadow: 'rgba(0, 0, 0, 0.05) 0px 10px 20px',
        transition: 'all 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s',
        color: '#000000',
        textDecoration: 'none',
        '&:hover': {
            boxShadow: 'rgb(0 0 0 / 10%) 0px 10px 20px',
            transform: 'translate3d(0px, -3px, 0px)'
        }
    },
    userAvatarHolder: {
        minWidth: '44px',
        minHeight: '44px',
        maxWidth: '44px',
        maxHeight: '44px',
        backgroundImage: 'linear-gradient(135deg, rgb(84, 188, 251), rgb(67, 66, 243))',
        backgroundColor: '#F2F2F2',
        backgroundSize: 'cover',
        backgroundPosition: ' center center',
        borderRadius: '999px',
    },
    userCText: {
        margin: '0px 0px 0px 16px',
        display: 'flex',
        flex: '1 1 auto',
        WebkitBoxAlign: 'center',
        alignItems: 'center',
    },
    itemContainer: {
        display: 'flex',
        WebkitBoxAlign: 'center',
        alignItems: 'center',
    },
    itemText: {
        fontSize: '18px',
        fontWeight: '600',
        color: '#000'
    },

    subMenuContainer: {
        paddingLeft: '24px',
        paddingRight: '24px',
    },
    subMenuItem: {
        display: 'flex',
        paddingTop: '20px',
        paddingBottom: '20px',
        borderBottomWidth: '1px',
        borderBottomStyle: 'solid',
        borderBottomColor: '#F2F2F2',
        transition: 'all 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s',
        WebkitBoxAlign: 'center',
        alignItems: 'center',
        cursor: 'pointer',
        textDecoration: 'none',
        color: 'inherit',
        willChange: 'transform',
        '&:hover': {
            transform: 'translate3d(3px, 0px, 0px)'
        }
    },
    subMenuItem2: {
        cursor: 'pointer',
        display: 'flex',
        flex: '1 1 auto',
        WebkitBoxAlign: 'center',
        alignItems: 'center',
        transition: 'all 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s',
    },
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
        opacity:'0.2'
    },

}));

const Dropdown = ({ id, handleLogOut }) => {

    const classes = useStyles()
    const [activeMenu, setActiveMenu] = useState('main');
    const [menuHeight, setMenuHeight] = useState(null);
    const dropdownRef = useRef(null);

    useEffect(() => {
        setMenuHeight(dropdownRef.current?.firstChild.offsetHeight)
      }, [])
    
      function calcHeight(el) {
        const height = el.offsetHeight;
        setMenuHeight(height);
      }
    
      function DropdownItem(props) {
        return (
          <a href="#" className="menu-item" onClick={() => props.goToMenu && setActiveMenu(props.goToMenu)}>
            {/* <span className="icon-button">{props.leftIcon}</span> */}
            {props.children}
            {/* <span className="icon-right">{props.rightIcon}</span> */}
          </a>
        );
      }

    return (
        <div className="dropdown" style={{ height: menuHeight }} ref={dropdownRef}>

        <CSSTransition
          in={activeMenu === 'main'}
          timeout={500}
          classNames="menu-primary"
          unmountOnExit
          onEnter={calcHeight}>
          <div className="menu">
           
                    <div className={classes.userItem}>
                        <Link to={`/${id}`} className={classes.userLink} >
                            <div className={classes.itemContainer}>
                                <div className={classes.userAvatarHolder}></div>
                                <div className={classes.userCText}>
                                    <div className={classes.itemText}>View Your Profile</div>
                                    <div className="css-5i9hgn">
                                        {/* ARROW SVG */}
                                    </div>
                                </div>
                            </div>
                        </Link>

                    </div>

                    {/* <DropdownItem> </DropdownItem>
            <DropdownItem> </DropdownItem>
            <DropdownItem> </DropdownItem> */}
             <div className={classes.subMenuItem}>
                            <div className={classes.subMenuItem2}>
                                <div className="css-pjy4z7">

                                </div>
                                <div className={classes.itemText}>Support</div>
                             </div>

                         </div>
           
           
            <div className={classes.subMenuItem}>
                             <div className={classes.subMenuItem2}>
                                <div className="css-pjy4z7">

                                </div>
                                <div onClick={handleLogOut} className={classes.itemText}>Disconnect</div>
                            </div>

                         </div>
           
  
            </div>

        </CSSTransition>
        </div>

    

    )
}

export default Dropdown